.about {
  flex-direction: column;
  margin-top: 10vh;
}


.about.fullscreen {
  height: 60vh;
}

.about__name {
  background: linear-gradient(var(--gradient-direction), #4158D0 0%, #C850C0 46%, #FFCC70 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text; /* fallback */
  color: var(--clr-primary); /* fallback */
}

.typer{
  margin-top: 0%;
}
.about > :nth-child(2) {
  margin-top: 1.2em;
  font-size: 2rem;
  line-height: 1.2;
  color: var(--clr-fg-alt);
}
.mobileHead{
  font-size: 4rem;
  text-align: center;
}
.about__desc {
  font-size: 1rem;
  max-width: 70%;
  text-align: center;
}

.about__desc,
.about__contact {
  margin-top: 2.4em;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .btn--outline {
  margin-right: 1em;
}
.btnResume{
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  border-radius: 5px;
  height: 50px;
  cursor: pointer;
  padding: 0px 30px;
  margin-top: 10px;
}
.btnResume:hover{
  background-color: var(--clr-primary);
  color: var(--clr-bg);
}


.scroll-down {
  position: absolute;
  bottom: 4em;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  cursor: pointer;
}

.scroll-down svg {
  color:var(--clr-primary);
  font-size: 1.5em;
}
.scroll-down svg {
  animation: heartbeat .5s infinite;
  animation-direction: alternate;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.3);
  }
  50% {
    transform: scale(1.7);
  }
  75% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

         
.btnResume{
  background-image: linear-gradient(to right, #DA22FF 0%, #9733EE  51%, #DA22FF  100%);

  margin-top: 3vh;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  border-radius: 30px;
  display: block;
  border-image:linear-gradient(to right, #DA22FF 0%, #9733EE  51%, #DA22FF  100%) ;
}

.btnResume:hover {
  background-position: right center; 
  color: #fff;
  text-decoration: none;
}



@media (max-width: 768) {
  .scroll-down {
    bottom: 6em;
  }
  .mobileHead{
    align-content: center;
    font-size: 2rem;
  }
  .about {
    flex-direction: column;
    margin-top: 15vh;
  }
  .app .about {
    align-items: flex-start;
    margin-top: 2em;
    justify-content: center;
    align-items: center;
  }
  .about > :nth-child(2) {
    font-size: 1.5rem;
  }
}
@media only screen and (max-width: 480px) {
  .mobileHead {
    font-size: 3rem;
  }

  .scroll-down {
    z-index: 0;
    
  }
  .about {
    flex-direction: column;
    margin-top: 7vh;
  }
  
  .about__desc {
    font-size: 1rem;
  }
  
  .about__contact {
    margin-top: 20px;
  }
  
  .btnResume {
    font-size: 0.8rem;
  }
  .typer{
    padding-left: 20px;
    
  }
}