.header {
  height: 8em;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  position: relative;
  z-index: 1;
 
}

.header.sticky {
  position: fixed;
  height: 3em;
  top: 0;
  left: 0;
  transform: translateY(-5%);
  z-index: 1;
    backdrop-filter: saturate(180%) blur(10px); 
  -webkit-backdrop-filter: saturate(180%) blur(10px); 
}

.header.sticky.show {
  transform: translateY(0%);
}

.empty-navbar {
  height: 8em;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  z-index: -1;
}
