.projects_container {
  width: 80%;
  margin: 0 auto;
  background-color: transparent;
}

.project {
  width: 100%;
  min-height: 40vh;
  /* height: 50vh; */
  margin: 6% 0% 6% 0%;
  box-shadow: var(--shadow);
  display: flex;
  transition: all 0.5s ease 0s;
  /* border: 2px solid var(--clr-primary); */
  flex-direction: column;
  --borderWidth: 20px;
  border-radius: var(--borderWidth);
  background: var(--clr-bg);
  position: relative;
  
}

.project {
  position: relative;
}
