.project_videocontainer {
  flex-basis: 65%;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.project_videocontainer > div {
  width: 90%;
  height: 90%;
  
  border-radius: 20px;
}
.project_videocontainer > div > img {
  width: 100%;
  height: 100%;
  border: 3px solid var(--clr-primary);
  border-radius: 20px;
}

.project_information {
  flex-basis: 35%;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0% 3% 4% 3%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-bottom: 10px;
}
.project_information > h2 {
  margin-top: 1em;
  color: var(--clr-primary);
}
.project_information > p {
  margin-top: 1em;
  text-align: center;
}
.project_information > div:nth-child(3) {
  margin-top: 0.5em;
  display: flex;
  justify-content: space-evenly;
}

.project_information > div:nth-child(3) > svg {
  font-size: 3em;
  margin: 2% 3% 2% 3%;
  color: var(--clr-primary);
  transition: all 0.2s ease-in-out;
}

.project_information > div:nth-child(3) > svg:hover {
  font-size: 3.3em;
}

.project_information > div:nth-child(4) {
  margin-top: 0.6em;
  display: flex;
}
.onbt {
  margin: 0 1vw 0 1vw;
}



.btns {
  position: relative;
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  border-radius: 30px;
  height: auto;
  cursor: pointer;
  padding: 10px;
  margin-top: 10px;
  z-index: 2;
}

.btns:hover {
  color: var(--clr-bg);
  border: 2px solid var(--clr-primary);
  background-color: var(--clr-primary);
}

/* Media query for smaller screens */
@media only screen and (max-width: 767px) {
  .btns {
    padding: 8px;
    font-size: 14px;
    margin-top: 8px;
  }
}









.project_information > h3 {
  margin-top: 1.6em;
  color: var(--clr-primary);
}
.project_information::-webkit-scrollbar {
  display: none;
}

.allProjects{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}


@media (max-width: 800px) {
  .project {
    flex-direction: column;
    /* height: 100vh; */
    min-height: 20vh;
  }
  .project_videocontainer {
    flex-basis: 40%;
  }
  .project_information {
    flex-basis: 60%;
  }
  .allProjects{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .projects_container {
    width: 100%;
    margin: 0 auto;
  }
}
