.footer {
  text-align: center;
  color: var(--clr-primary);
}

.footerBox{
  /* border: 3px solid var(--clr-primary); */
  width: 50%;
  margin: auto;
  padding-bottom: 20px;
  border-radius: 15px;
  background-color: transparent;
  padding: 20px;
  height: auto;
}

@media (max-width: 600px) {
  .footerBox{
    /* border: 3px solid var(--clr-primary); */
    width: 100%;
    border-radius: 0px;
    margin-bottom: 0px;
  }
}


.heart{
  color: red;
  background: transparent;
  animation: pulsate 1s linear infinite;
  /* -webkit-animation: heartbeat 1s infinite;  */
}


@keyframes heartbeat {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}

@-webkit-keyframes heartbeat { /* add vendor prefix to keyframes */
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
}

@keyframes pulsate {
  0% {
    color: red;
  }
  50% {
    color: pink;
  }
  100% {
    color: red;
  }
}


@keyframes spin {
  0% {
    color: red;
    transform: rotate(0deg);
  }
  100% {
    color: red;
    transform: rotate(360deg);
  }
}